








import blockMixin from './../BlockMixin.vue'

export default {
  name: 'quote'

  mixins: [blockMixin]

  data: -> {
    fields: {
      quote: ''
      cite: ''
    }
  }

  mounted: ->
    this.$el.querySelector('.bb8-form-control').focus() if this.block.fields.quote.length <= 0

  methods: {
    removeBlock: (event) ->
      return if event.target.value.length > 0
      this.$emit('remove-block', this.index)
  }
}



